import * as OfficeHelpers from '@microsoft/office-js-helpers';
import { OutlookHelper, Ph2ApiHandler } from '../utils';
import { ScamReportService } from '../service/ScamReportService';
import { ph2Url, dialogUrl } from '../config';

const CONFIRM_DIALOG_HEIGHT = 10;
const CONFIRM_DIALOG_WIDTH = 29;
const SUCCESS_DIALOG_BASE_HEIGHT = 9;
const SUCCESS_DIALOG_PC_BASE_HEIGHT = 12;
const SUCCESS_DIALOG_BASE_WIDTH = 37;

Office.initialize = () => {
    console.log('Office initialize');
    if (OfficeHelpers.Authenticator.isAuthDialog()) return;
};

async function action (event) {
    await processReport();
    event.completed();
}

async function ribbonAction (event) {
    let options = await processReport(true);
    event.completed(options);
}

function logMessage(id, logger) {
    return function (message) {
        if (logger && logger.captureMessage) {
            return logger.captureMessage(`processId ${id}: ${message}`);
        }
    }
}

async function processReport(isAlternative = false) {
    const search = isAlternative && Office.context.platform == Office.PlatformType.PC ? Office.context.urls.javascriptRuntimeUrl : window.location.search;
    const query = new URLSearchParams(search);
    const apiBase64 = query.get('ph2API') || '';
    const apiUrl = window.atob(apiBase64) || ph2Url;
    const cellId = query.get('cellId');
    const authToken = query.get('token');
    const collectorId = Date.now();
    const log = logMessage(collectorId);
    const ph2ApiHandler = new Ph2ApiHandler(apiUrl, authToken, cellId, log);
    const scamReportService = new ScamReportService(apiUrl, cellId, authToken, log);

    try {
        const deviceType = detectDeviceType();
        const userSettings = await initializeHandlers(ph2ApiHandler, scamReportService);

        if (!isAlternative && !await confirmReport(userSettings, deviceType)) return;

        const [emailId, sharedUserMail] = await Promise.all([
            OutlookHelper.getCurrentEmailItemRestId(),
            OutlookHelper.getSharedUserMail()
        ]);
        const reportType = await scamReportService.reportEmail(emailId, sharedUserMail, isAlternative);

        if (isAlternative) {
            return preparePostReportDialogOptions(reportType, userSettings);
        } else {
            await displaySuccessDialog(userSettings, reportType, deviceType);
            await finalizeReport(reportType, scamReportService, emailId, sharedUserMail, userSettings);
        }
    } catch (error) {
        await handleError(error, ph2ApiHandler);
        return {
            moveItemTo: Office.MailboxEnums.MoveSpamItemTo.NoMove
        }
    }
}

function detectDeviceType() {
    switch (Office.context.platform) {
        case Office.PlatformType.iOS:
        case Office.PlatformType.Android:
            return "mobile";
        case Office.PlatformType.PC:
        case Office.PlatformType.Mac:
            return "pc";
        default:
            return "owa";
    }
}

async function initializeHandlers(ph2ApiHandler, scamReportService) {
    await ph2ApiHandler.initSetting();
    await scamReportService.init();
    return ph2ApiHandler.getUserCustomData(Ph2ApiHandler.setting);
}

async function confirmReport(userSettings, deviceType) {
    const showConfirmDialog = OutlookHelper.getShowConfirmSetting(userSettings);
    if (showConfirmDialog) {
        try {
            const result = await OutlookHelper.showDialog(
                `${dialogUrl}?dialogType=confirm&deviceType=${deviceType}&allowConfigure=${userSettings.allow_configure_confirm}`,
                CONFIRM_DIALOG_HEIGHT,
                CONFIRM_DIALOG_WIDTH
            );
            return result.ok;
        } catch (error) {
            throw new Error(`Confirmation dialog failed: ${error.message}`);
        }
    }
    return true;
}

async function displaySuccessDialog(userSettings, reportType, deviceType) {
    const showSuccessDialog = OutlookHelper.getShowNotiSetting(userSettings);
    if (showSuccessDialog) {
        const message = getNotificationMessage(reportType, userSettings);
        if (message) {
            const [adjustedWidth, adjustedHeight] = adjustSuccessDialogSizeForMessage(message, deviceType);
            await OutlookHelper.showDialog(
                `${dialogUrl}?dialogType=success&deviceType=${deviceType}&allowConfigure=${userSettings.allow_configure_noti}&ms=${window.btoa(message)}`,
                adjustedHeight,
                adjustedWidth
            );
        }
    }
}

function getNotificationMessage(reportType, userSettings) {
    switch (reportType) {
        case 0:
            return userSettings.show_simulated ? userSettings.simulated_content : "";
        case 1:
            return userSettings.show_message_real ? userSettings.message_real : "";
        case 2:
            return userSettings.show_simulated_company ? userSettings.simulated_company_content : "";
        case 3:
            return userSettings.show_simulated ? userSettings.training_email_content : "";
        default:
            return "";
    }
}

function adjustSuccessDialogSizeForMessage(message, deviceType) {
    const sizeMatch = /data-size="(\d\d)\|(\d\d)"/.exec(message);
    if (sizeMatch) {
        let width = parseInt(sizeMatch[1]);
        let height = parseInt(sizeMatch[2]);
        return [width, height];
    }

    let height = deviceType === "pc" ? SUCCESS_DIALOG_PC_BASE_HEIGHT : SUCCESS_DIALOG_BASE_HEIGHT;
    if (message.length > 100) {
        height += Math.ceil((message.length - 100) / 36);
    }
    return [SUCCESS_DIALOG_BASE_WIDTH, height]
}

async function finalizeReport(reportType, scamReportService, emailId, sharedUserMail, userSettings) {
    if ((reportType !== 2 || !userSettings.dont_delete_email) && reportType !== 3) {
        await scamReportService.cleanupPhishingEmail(emailId, sharedUserMail);
    }
}

function preparePostReportDialogOptions(reportType, userSettings) {
    const showSuccessDialog = OutlookHelper.getShowNotiSetting(userSettings);
    let description = showSuccessDialog ? getNotificationMessage(reportType, userSettings) : "";
    if (description) {
        description = description.replace(/<[^>]*>/g, ""); // Remove HTML tags
    }
    const moveSpamItemTo = (reportType !== 2 || !userSettings.dont_delete_email) && reportType !== 3
        ? Office.MailboxEnums.MoveSpamItemTo.DeletedItemsFolder
        : Office.MailboxEnums.MoveSpamItemTo.NoMove;

    return {
        moveItemTo: moveSpamItemTo,
        showPostProcessingDialog: {
            title: userSettings.name,
            description: description
        }
    };
}

async function handleError(error, ph2ApiHandler) {
    const genericErrorMessage = "Unexpected error occurred. Please contact IT support.";
    await OutlookHelper.showError(genericErrorMessage);
    await ph2ApiHandler.saveErrorLog(error, cellId);
}

Office.actions.associate("action", action);
Office.actions.associate("ribbonAction", ribbonAction);
