import axios from "axios"

export async function requestTo(url,action, options) {
    const {method, data, headers} = options;
    return await axios({
        method,
        baseURL: url+action,
        headers,
        data
    }).then((response) => {
        console.log("sucessfully send request to: " + action);
        console.log(response);
        return response.data
    }).catch((ex) => {
        console.error(`Failed to send the request to: ${action}, ${ex.message}`);
        if (action !== "/outlook_addins/save_error_logs") {
            ex.message = `Call ${action} failed: ${ex.message}`;
            throw ex;
        }
    })

    // return new Promise((resolve, reject) => {
    //     $.ajax({
    //         url,
    //         method,
    //         contentType,
    //         //dataType,
    //         data, //use json.stringify when you post json as body
    //         headers
    //     }).done(function (result) {
    //         console.log("sucessfully send request to: " + url)
    //         console.log(result)
    //         resolve(result)
    //     }).fail(function (error) {
    //         console.log("Error happen when sending request to: " + url)
    //         console.log(error)
    //         reject(error)
    //         // Handle error
    //     })
    // })
}