import {requestTo} from "./helper"
import {loginCallbackUrl} from "../config"
import {OutlookHelper} from "../utils"

export class Ph2ApiHandler {
    constructor(ph2API, accessToken, cellId, collect) {
        if (!ph2API || !accessToken || !cellId) {
            throw new Error("ph2API, cellId or cellToken is invalid")
        }
        this.token = { 'Authorization': 'Token ' + accessToken };
        this.ph2API = ph2API;
        this.cellId = cellId;
        this.collect = collect
    }

    async _callPh2RestApi(action, options) {
        const start = (new Date()).getTime();
        const result = await requestTo(this.ph2API, action, options);
        if (this.collect) {
            this.collect('request to ' + action + ', time: ' + ((new Date()).getTime() - start) + ' ms')
        }
        return result
    }

    async getReportConfig(data) {
        return await this._callPh2RestApi("/outlook_addins/get_report_config", {
            method: 'POST',
            contentType: 'application/json',
            data,
            headers: this.token
        })
    }

    async isSystemEmail(data) {
        return await this._callPh2RestApi("/outlook_addins/is_system_email", {
            method: 'POST',
            contentType: 'application/json',
            data,
            headers: this.token
        })
    }

    async writePhishingReport(data) {
        return await this._callPh2RestApi("/outlook_addins/report_phishing", {
            method: 'POST',
            contentType: 'application/json',
            data,
            headers: this.token
        })
    }

    async writePhishingReportOldVersion(data) {
        return await this._callPh2RestApi("/outlook_addins", {
            method: 'POST',
            contentType: 'application/json',
            data,
            headers: this.token
        })
    }

    static setting = "user_setting";
    static graph = "graph_token";

    getUserCustomData(dataType=Ph2ApiHandler.setting) {
        if(![Ph2ApiHandler.setting, Ph2ApiHandler.graph].includes(dataType)){
            throw new Error("Our add in does not support setting " + dataType)
        }

        return OutlookHelper.getLocalStorage(dataType)
    }

    // ONLY FIRST TIME CALLING
    // This function is used to init user setting cached in the server at the first time the add in is initialized
    async initSetting() {
        const userEmail = OutlookHelper.getCurrentEmailAddress();
        const serverSettingResult = await this.getReportConfig({
            "cell_id": this.cellId,
            "reporter_email": userEmail
        });
        const serverSetting = serverSettingResult && serverSettingResult.data;
        await OutlookHelper.setLocalStorage(Ph2ApiHandler.setting, serverSetting);
    }

    async saveErrorLog(ex, cellId) {
        const setting = this.getUserCustomData(Ph2ApiHandler.setting);
        if (setting.send_error !== true) { return; }

        const message = ex.message || "N/A";
        const reporterEmail = OutlookHelper.getCurrentEmailAddress();

        let data = {
            "cell_id": cellId,
            "reporter_email": reporterEmail,
            "add_in_version": "O365 - Outlook Web Add-in",
            "error_message": message
        };

        if (ex instanceof TypeError || message.endsWith("undefined") || message.endsWith("null")) {
            data['error_code'] = "NullReferenceException";
            data['error_code_type'] = 2;
        }
        else if (message.includes("Timeout")) {
            data['error_code'] = "TimeoutException";
            data['error_code_type'] = 3;
        }
        else {
            data['error_code'] = "OtherException";
            data['error_code_type'] = 4;
        }

        return await this._callPh2RestApi("/outlook_addins/save_error_logs", {
            method: 'POST',
            contentType: 'application/json',
            data,
            headers: this.token
        })
    }

    async getGraphToken(data) {
        const staticData = {
            cell_id: this.cellId,
            redirect_uri: loginCallbackUrl
        };
        const finalData = Object.assign({}, staticData, data);
        const result = await this._callPh2RestApi("/outlook_addins/get_token_from_third_party", {
            method: 'POST',
            data: finalData,
            headers: this.token
        });

        return result && result.data
    }

    async moveEmail(emailId, destinationId, accessToken) {
        await this._callPh2RestApi("/outlook_addins/move_email", {
            method: 'POST',
            headers: this.token,
            data: {
                cell_id: this.cellId,
                item_id: emailId,
                destination_id: destinationId,
                access_token: accessToken
            }
        });
    }
}
