import { requestTo } from './helper'
import { graphUrl } from '../config'

export class GraphApiHandler {
    constructor(accessToken, collect) {
        this.token = { 'Authorization': 'Bearer ' + accessToken };
        this.collect = collect
    }

    async _callGraphRestApi(action, options, userMail) {
        const url = graphUrl + (userMail ? `/users/${userMail}` : '/me')
        const start = (new Date()).getTime();
        const result = await requestTo(url, action, options);
        if (this.collect) {
            this.collect('request to ' + action + ', time: ' + ((new Date()).getTime() - start) + ' ms');
        }
        return result
    }

    async getUserInfo() {
        return await this._callGraphRestApi('', {
            method: 'GET',
            headers: this.token
        });
    }

    async getEmail(emailId, userMail) {
        return await this._callGraphRestApi('/messages/' + emailId, {
            method: 'GET',
            headers: this.token
        }, userMail);
    }

    async getEmailInternetHeader(emailId, userMail) {
        return await this._callGraphRestApi(`/messages/${emailId}/?$select=internetMessageHeaders`, {
            method: 'GET',
            headers: this.token
        }, userMail);
    }

    async getEmailMIMEContent(emailId, userMail) {
        return await this._callGraphRestApi(`/messages/${emailId}/$value`, {
            method: 'GET',
            headers: this.token
        }, userMail);
    }

    async moveEmail(emailId, userMail, destinationId) {
        const movingResult = await this._callGraphRestApi(`/messages/${emailId}/move`, {
            method: 'POST',
            data: { destinationId },
            headers: this.token
        }, userMail);

        console.log('moved mail ', movingResult)
    }

    async sendEmail(data) {
        const sendingResult = await this._callGraphRestApi('/sendmail', {
            method: 'POST',
            data,
            headers: this.token
        });

        console.log('sent mail ', sendingResult);
    }
}