import { Buffer } from 'buffer';
import { GraphApiHandler, OutlookHelper, Ph2ApiHandler, Authenticator } from '../utils';

export class ScamReportService {
    constructor(ph2API, cellId, ph2Token, collect) {
        this.ph2API = ph2API;
        this.cellId = cellId;
        this.ph2Token = ph2Token;
        this.ph2ApiHandler = null;
        this.collect = collect
    }

    async init() {
        this.ph2ApiHandler = new Ph2ApiHandler(this.ph2API, this.ph2Token, this.cellId, this.collect);
        console.log("Phish report service is ready to use")
    }

    extractTokenFromMailBody(body = "") {
        const LINK_PATTERN = /http((?!"|%22).)+/gi;
        const TOKEN_PATTERN = /[=\/][-\w]{32}/g;

        if (body) {
            let linkMatches = body.match(LINK_PATTERN) || [];
            for (let linkMatch of linkMatches) {
                try {
                    linkMatch = decodeURIComponent(linkMatch)
                } catch {}
                let tokenMatches = linkMatch.match(TOKEN_PATTERN);
                if (tokenMatches && tokenMatches.length === 2) {
                    return { token1: tokenMatches[0].slice(1), token2: tokenMatches[1].slice(1) }
                }
            }
        }
        return { token1: "", token2: "" }
    }

    async reportEmail(emailId, userMail, isAlternative) {
        let messageResult;
        if (isAlternative && Office.context.platform == Office.PlatformType.PC) {
            messageResult = await Promise.all([Office.context.mailbox.item, OutlookHelper.getInternetHeaders(), Office.context.mailbox.userProfile, OutlookHelper.getEmailMimeContent()]);
        } else {
            const graphToken = await Authenticator.process(this.ph2ApiHandler);
            const graphApiHandler = new GraphApiHandler(graphToken.raw.access_token, this.collect);
            messageResult = await Promise.all([graphApiHandler.getEmail(emailId, userMail), graphApiHandler.getEmailInternetHeader(emailId, userMail), graphApiHandler.getUserInfo(), graphApiHandler.getEmailMIMEContent(emailId, userMail)]);
        }

        const startGetUserAndEmail = (new Date()).getTime();
        const endGet = (new Date()).getTime();

        if (this.collect) {
            this.collect('!!end call graph to get user, email info ' + (endGet - startGetUserAndEmail) + ' ms')
        }

        const userInfo = messageResult[2];
        if (!userInfo) {
            throw new Error("Error getting user information by calling Graph")
        }

        const reporterEmail = OutlookHelper.getCurrentEmailAddress();

        const reporterName = userInfo.displayName || OutlookHelper.getCurrentDisplayName();

        const config = this.ph2ApiHandler.getUserCustomData(Ph2ApiHandler.setting);

        if (!config) {
            throw new Error('Cannot get configuration of cell ' + this.cellId + ' reporter email ' + reporterEmail)
        }

        const currentMessage = messageResult[0];
        if (!currentMessage) {
            throw new Error('Cannot read the current email')
        }

        if (currentMessage["@odata.context"]) {
            delete currentMessage["@odata.context"];
            console.log("cleanup unused info for attachment")
        }

        const txtNoSubject = "[No Subject]"
        if (currentMessage.subject === null || currentMessage.subject === "") {
            currentMessage.subject = txtNoSubject
        }

        const startToken = (new Date()).getTime();

        const currentMessageBody = currentMessage.body && currentMessage.body.content;
        const tokenResult = this.extractTokenFromMailBody(currentMessageBody);

        const endToken = (new Date()).getTime();

        if (this.collect) {
            this.collect('!!end extract token ' + (endToken - startToken) + ' ms')
        }

        const token1 = tokenResult && tokenResult.token1;
        const token2 = tokenResult && tokenResult.token2;

        // if result == true => email_type=0 (system) else email_type=1 (others)
        let source_email;

        const startHead = (new Date()).getTime();
        const EXCHANGE_SERVER_PATTERN = /\.outlook\.com|localhost\.localdomain|127\.0\.0\.1/i;
        const RECEIVED_PATTERN = /^(from[\s\S]*?)?(by[\s\S]*?)?(via[\s\S]*?)?(with[\s\S]*?)?(id[\s\S]*?)?(for[\s\S]*?)?$/i;
        const currentMessageHeaders = messageResult[1];

        try {
            if (currentMessageHeaders && currentMessageHeaders.internetMessageHeaders) {
                let ips = [];
                let domains = [];
                let addresses = currentMessageHeaders.internetMessageHeaders.filter(function (item) {
                    return item.name.toLowerCase() === "received";
                });
                for (let i = 1; i <= 2 && addresses.length > 0; i++) {
                    let lastAddress = addresses.pop().value;
                    if (lastAddress.toLowerCase().startsWith("by") && addresses.length > 0) {
                        let temp = addresses.pop().value;
                        if (temp.toLowerCase().startsWith("from")) {
                            i++;
                            lastAddress = temp;
                        }
                    }
                    const receivedMatch = RECEIVED_PATTERN.exec(lastAddress);
                    lastAddress = receivedMatch[1] || receivedMatch[2];
                    if (lastAddress && !EXCHANGE_SERVER_PATTERN.test(lastAddress)) {
                        const ipPattern = /(\d+\.){3}\d+|IPv6:|([A-Fa-f0-9]+:{1,2}){1,7}[A-Fa-f0-9]+/g;
                        const domainPattern = /([A-Za-z0-9-]+\.)+[A-Za-z]+/g;
                        ips = lastAddress.match(ipPattern) || [];
                        domains = lastAddress.match(domainPattern) || [];
                        break;
                    }
                }
                if (ips.length === 0 && domains.length === 0) {
                    console.log('Authentication-Results');
                    addresses = currentMessageHeaders.internetMessageHeaders.filter(function (item) {
                        return item.name.toLowerCase() === "authentication-results"
                    });
                    if (addresses.length > 0) {
                        const ipPattern = /\(.*?((\d+\.){3}\d+|([A-Fa-f0-9]+:{1,2}){1,7}[A-Fa-f0-9]+).*?\)/g;
                        const domainPattern = /=(.*@)?(([A-Za-z0-9-]+\.)+[A-Za-z0-9]+)/g;

                        while (true) {
                            const ipMatches = ipPattern.exec(addresses[0].value);
                            if (!ipMatches) break;

                            const ip = ipMatches[1];
                            if (!ips.includes(ip)) {
                                ips.push(ip);
                            }
                        }
                        while (true) {
                            const domainMatches = domainPattern.exec(addresses[0].value);
                            if (!domainMatches) break;

                            const domain = domainMatches[2];
                            if (!domains.includes(domain)) {
                                domains.push(domain);
                            }
                        }
                    }
                }
                source_email = {
                    ips,
                    domains
                }
            }
        } catch (ex) {
            ex.message = "Extract header failed: " + ex.message;
            await this.ph2ApiHandler.saveErrorLog(ex, this.cellId);
        }
        const endHead = (new Date()).getTime();

        if (this.collect) {
            this.collect('!!end header extract ' + (endHead - startHead) + ' ms')
        }

        let isSysEmailParam = {
            "cell_id": this.cellId,
            "reporter_email": reporterEmail,
            "token1": token1,
            "token2": token2,
            "source_email": JSON.stringify(source_email),
            "reported_phishing_email_from": currentMessage.sender.emailAddress.address
        };

        const reportData = new FormData();
        reportData.append('cell_id', this.cellId);
        reportData.append('reporter_email', reporterEmail);
        reportData.append('reporter_email_subject', currentMessage.subject);
        reportData.append('reporter_name', reporterName);
        reportData.append('reported_phishing_email_from', currentMessage.sender.emailAddress.address);
        reportData.append('phishing_email_received_date', currentMessage.receivedDateTime);
        reportData.append('token1', token1);
        reportData.append('token2', token2);
        reportData.append('source_email', isSysEmailParam.source_email);
        reportData.append('access_type', OutlookHelper.getHost());

        //sendEmail and checkSysEmail
        const startReport = (new Date()).getTime();
        const resultOfCheckEmail = await this.ph2ApiHandler.isSystemEmail(isSysEmailParam)

        let reportResultPromises = [];

        //prepare sending email
        let toRecipients = [];
        let ccRecipients = [];
        if (config.report_email && config.report_email !== '') {
            toRecipients = config.report_email.split(',').map(x => x.trim()).filter(y => y !== '')
        }

        if (config.send_copy && config.send_copy === true && config.admin_email && config.admin_email !== '' && config.report_email !== config.admin_email) {
            ccRecipients = config.admin_email.split(',').map(x => x.trim()).filter(y => y !== '')
        }

        if (toRecipients.length !== 0) {
            console.log("There are recipients to send email");
            const emailContent = reporterEmail + " has reported a phishing email.<br><br>"
                + "Subject: " + currentMessage.subject + "<br><br>"
                + "Sender: " + currentMessage.sender.emailAddress.address + "<br><br>"
                + "Please find the details in attached email.<br><br>"
                + "Warning: the attachment may contain malware. Please be careful when dealing with potential phishing emails.";

            let attachmentName = "";
            if (/["\\|<>\/]/g.test(currentMessage.subject)) {
                attachmentName = encodeURI(currentMessage.subject)
            } else {
                attachmentName = currentMessage.subject
            }

            console.log("is_system_email", resultOfCheckEmail.result);

            if (resultOfCheckEmail.result === 1) {
                // send email here
                const emailMIMEContent = Buffer.from(messageResult[3]);
                const sendmailtemp = {
                    "message": {
                        "subject": "Phishing Report from " + currentMessage.sender.emailAddress.address + " - " + currentMessage.subject,
                        "body": {
                            "contentType": "html",
                            "content": emailContent
                        },
                        "toRecipients": toRecipients.map(x => ({
                            "emailAddress": {
                                "address": x
                            }
                        })),
                        "bccRecipients": ccRecipients.map(x => ({
                            "emailAddress": {
                                "address": x
                            }
                        })),
                        "attachments": [
                            {
                                "@odata.type": "#microsoft.graph.fileAttachment",
                                "name": `${attachmentName}.eml`,
                                "contentType": "application/octet-stream",
                                "contentBytes": emailMIMEContent.toString("base64")
                            }
                        ]
                    }
                };

                // reportResultPromises.push(graphApiHandler.sendEmail(sendmailtemp));
                if (config.is_send_to_triage) {
                    reportData.append('email_file', new Blob([messageResult[3]], {type: 'application/octet-stream'}), 'email_file.eml');
                }
            }
        }

        const endReport = (new Date()).getTime();

        if (this.collect) {
            this.collect('!!end send email ' + (endReport - startReport) + ' ms')
        }

        const reportResult = resultOfCheckEmail;//reportResultArr[0];
        console.log(reportResult)

        //report
        reportData.append('email_type', reportResult.result);
        await this.ph2ApiHandler.writePhishingReportOldVersion(reportData);

        const endReport1 = (new Date()).getTime();

        if (this.collect) {
            this.collect('!!end report email ' + (endReport1 - endReport) + ' ms')
        }

        return resultOfCheckEmail.result;
    }

    async cleanupPhishingEmail(emailId, userMail) {
        const destinationId = 'DeletedItems';
        const graphToken = await Authenticator.process(this.ph2ApiHandler);
        if (!userMail && Office.context.platform === Office.PlatformType.OfficeOnline) {
            await this.ph2ApiHandler.moveEmail(emailId, destinationId, graphToken.raw.access_token);
        } else {
            const graphApiHandler = new GraphApiHandler(graphToken.raw.access_token, this.collect);
            await graphApiHandler.moveEmail(emailId, userMail, destinationId);
        }
    }
}