import * as OfficeHelpers from '@microsoft/office-js-helpers'
import { appId, loginCallbackUrl, addInScope } from '../config'
const jwtDecode = require('jwt-decode');

export class OutlookHelper {

    static getHost() {
        console.log(Office.context.mailbox.diagnostics.hostName);
        console.log(navigator);
        return Office.context.mailbox.diagnostics.hostName
    }

    static getCurrentEmailAddress() {
        return Office.context.mailbox.userProfile.emailAddress
    }

    static getCurrentDisplayName() {
        return Office.context.mailbox.userProfile.displayName
    }

    static async getCurrentEmailBody(type = Office.CoercionType.Html) {
        return new Promise((resolve, reject) => {
            Office.context.mailbox.item.body.getAsync(type,
                (result) => {
                    console.log(JSON.stringify(result));
                    resolve(result)
                }
            )
        })
    }

    static async getInternetHeaders() {
        return new Promise((resolve, reject) => {
            Office.context.mailbox.item.getAllInternetHeadersAsync((result) => {
                if (result.status === Office.AsyncResultStatus.Succeeded) {
                    resolve(result.value);
                } else {
                    reject(result.error);
                }
            });
        });
    }

    static async getEmailMimeContent() {
        return await new Promise((resolve, reject) => {
            Office.context.mailbox.item.getAsFileAsync({ asyncContext: null }, (result) => {
                if (result.status === Office.AsyncResultStatus.Succeeded) {
                    resolve(result.value);
                } else {
                    reject(result.error.message);
                }
            });
        });
    }

    static async getCurrentEmailBodyAsHtml() {
        const body = await this.getCurrentEmailBody(Office.CoercionType.Html);
        return body && body.value || null
    }

    static async getBoostrapGraphToken(options) {
        return new Promise((resolve, reject) => {
            Office.context.auth.getAccessTokenAsync(options,
                (result) => {
                    console.log(result);
                    resolve(result)
                }
            )
        })
    }

    static async promptLogin(responseType='token', force=true, atempt = 0) {
        // cannot use SSO
        // using Authorization code grant flow
        console.log("login user without SSO");
        const authenticator = new OfficeHelpers.Authenticator();
        responseType = responseType+"&prompt=select_account";
        authenticator.endpoints.registerMicrosoftAuth(appId, { redirectUrl: loginCallbackUrl, scope: addInScope, responseType });

        try{
            const token = await authenticator.authenticate(OfficeHelpers.DefaultEndpoints.Microsoft, force);
            console.log(token);
            return token ? { raw: token, type: (responseType==='token'? "graph":"authorization") } : null
        } catch (ex){
            if(ex && ex.message && ex.message.indexOf("Unable to delete 'Microsoft' from storage") >= 0 && atempt < 1){
                //this is for the fix that
                atempt = atempt + 1;
                console.log("while authenticate", ex);
                return await OutlookHelper.promptLogin(responseType, force, atempt)
            } else {
                ex.message = "promptLogin failed: " + ex.message;
                throw ex;
            }
        }

    }

    static getCurrentEmailItemRestId() {
        if (Office.context.mailbox.diagnostics.hostName === 'OutlookIOS') {
            // itemId is already REST-formatted
            return Office.context.mailbox.item.itemId;
        } else {
            // Convert to an item ID for API v2.0
            return Office.context.mailbox.convertToRestId(
                Office.context.mailbox.item.itemId,
                Office.MailboxEnums.RestVersion.v2_0
            );
        }
    }

    static checkEmailToken(token) {
        console.log("Validate token...");

        let  decodedToken = null;

        try{
            decodedToken = jwtDecode(token)
        } catch (ex) {
            ex.message = "jwtDecode failed: " + ex.message;
            throw ex;
        }

        const user = decodedToken && (decodedToken.preferred_username || decodedToken.email || decodedToken.unique_name || decodedToken.upn);
        console.log('raw token decoded info', JSON.stringify(decodedToken));
        console.log('usermail from jwt', user && user.toLowerCase());

        const jwtEmails = [
            decodedToken.preferred_username && decodedToken.preferred_username.toLowerCase(),
            decodedToken.email && decodedToken.email.toLowerCase(),
            decodedToken.unique_name && decodedToken.unique_name.toLowerCase(),
            decodedToken.upn && decodedToken.upn.toLowerCase()];
        const mailBox = OutlookHelper.getCurrentEmailAddress() && OutlookHelper.getCurrentEmailAddress().toLowerCase()
        console.log('jwtEmails', jwtEmails);
        console.log('JS API', mailBox);
        if (user && !(jwtEmails.includes(mailBox))) {
            throw new Error("User should sign in with the same credentials as the user's mailbox")
        }
        console.log("token match for user ", user)
    }

    static setRoamingStorage(name, value) {
        return new Promise((resolve, reject) => {
            Office.context.roamingSettings.set(name, value);
            Office.context.roamingSettings.saveAsync((asyncResult) => {
                if (asyncResult.status == Office.AsyncResultStatus.Failed) {
                    console.log(JSON.stringify(asyncResult));
                    reject(asyncResult)
                } else {
                    resolve(asyncResult)
                }
            })
        })
    }

    static getRoamingStorage(name) {
        return Office.context.roamingSettings.get(name);
    }

    static setLocalStorage(name, value) {
        if (typeof localStorage === 'undefined') {
            this.setRoamingStorage(name, JSON.stringify(value));
        } else {
            localStorage.setItem(name, JSON.stringify(value));
        }
    }

    static getLocalStorage(name) {
        const result = typeof localStorage === 'undefined' ? this.getRoamingStorage(name) : localStorage.getItem(name);
        return result ? JSON.parse(result) : null;
    }

    static removeLocalStorage(name) {
        typeof localStorage === 'undefined' ? this.deleteRoamingStorage(name) : localStorage.removeItem(name);
    }

    static deleteRoamingStorage(name) {
        console.log("remove roaming: ", name);
        return new Promise((resolve, reject) => {
            Office.context.roamingSettings.remove(name);
            Office.context.roamingSettings.saveAsync((asyncResult) => {
                if (asyncResult.status == Office.AsyncResultStatus.Failed) {
                    // Handle the failure.
                    console.log(JSON.stringify(asyncResult));
                    reject(asyncResult)
                } else {
                    resolve(asyncResult)
                }
            })
        })

    }

    static async showResult(data, type='progressIndicator') {
        const key = "progress";
        await OutlookHelper.closeNotification(key);

        OutlookHelper.showNotification(key, data, type)
    }

    static showNotification(key, data, type){
        if (data !== '' && data.length > 150) {
            data = data.substr(0, 147) + '...'
        }

        console.log("show notification: ", data);

        //type can be errorMessage || progressIndicator || informationalMessage
        if (['errorMessage', 'progressIndicator', 'informationalMessage'].indexOf(type) < 0) {
            type = 'errorMessage'
        }

        Office.context.mailbox.item.notificationMessages.addAsync(key, {
            type,
            message: data
        });
    }

    static async showError(data, type='errorMessage'){
        const key = "error";
        await OutlookHelper.closeNotification(key);

        OutlookHelper.showNotification(key, data, type)
    }

    static closeNotification(key){
        if(['progress','error'].indexOf(key) < 0){
            key = 'progress'
        }
        return new Promise((resolve, reject) => {
            Office.context.mailbox.item.notificationMessages.removeAsync(key, function(a){
                resolve()
            });

        })

    }

    static cleanUpNotification(){
        return new Promise((resolve, reject) => {
            Office.context.mailbox.item.notificationMessages.getAllAsync(function (asyncResult) {

                if (asyncResult.value.length > 0) {
                    const cleanUpPromise = [];
                    for(const value of asyncResult.value){
                        console.log(value);
                        cleanUpPromise.push(OutlookHelper.closeNotification(value.key))
                    }

                    return Promise.all(cleanUpPromise).then(function(){
                        resolve()
                    })
                }
            });

        })

    }

    static dialogCloseAsync(dialog, asyncResult) {
        // issue the close
        dialog.close();
        // and then try to add a handler
        // when that fails it is closed
        setTimeout(function () {
            try {
                dialog.addEventHandler(Office.EventType.DialogMessageReceived, function () { });
                dialogCloseAsync(dialog, asyncResult);
            } catch (e) {
                asyncResult(); // done - closed
            }
        }, 1000);
    }

    static showDialog(url, height, width, logger=null) {
        let that = this;
        return new Promise((resolve, reject) => {
            let dialog = null;
            Office.context.ui.displayDialogAsync(encodeURI(url), { height: height, width: width, displayInIframe: true },
                function (asyncResult) {
                    if (asyncResult.status === Office.AsyncResultStatus.Failed) {
                        reject(new Error('displayDialog failed: ' + asyncResult.error.message));
                    } else {
                        dialog = asyncResult.value;
                        dialog.addEventHandler(Office.EventType.DialogMessageReceived, (arg) => {
                            let result = JSON.parse(arg.message);
                            console.log(result);
                            that.dialogCloseAsync(dialog, () => {
                                console.log('dialog ' + url + " - function closed process completed");
                                resolve(result)
                            })

                        });
                        dialog.addEventHandler(Office.EventType.DialogEventReceived, arg => {
                            let error = null;
                            switch (arg.error) {
                                case 12002:
                                    error = "The dialog box has been directed to a page that it cannot find or load, or the URL syntax is invalid.";
                                    break;
                                case 12003:
                                    error = "The dialog box has been directed to a URL with the HTTP protocol. HTTPS is required.";
                                    break;
                                case 12006:
                                    error = "Dialog closed.";
                                    break;
                                default:
                                    error = "Unknown error in dialog box.";
                                    break;
                            }
                            that.dialogCloseAsync(dialog, () => {
                                console.log('dialog ' + url + "function closed process completed but dialog has error");
                                reject(new Error('Dialog closed with error: ' + error));
                            })
                        });
                    }
                }
            )
        })
    }

    static getShowConfirmSetting(settings) {
        const showConfirm = 2;
        settings.allow_configure_confirm = (settings.ignore_user_settings & showConfirm) === 0
        if (settings.show_confirm && settings.allow_configure_confirm) {
            return !OutlookHelper.getRoamingStorage("dontShowConfirm");
        }
        return settings.show_confirm;
    }

    static getShowNotiSetting(settings) {
        const showNoti = 4;
        settings.allow_configure_noti = (settings.ignore_user_settings & showNoti) === 0
        if (settings.show_thank_you && settings.allow_configure_noti) {
            return !OutlookHelper.getRoamingStorage("dontShowNoti");
        }
        return settings.show_thank_you;
    }

    static async getSharedUserMail() {
        return new Promise((resolve) => {
            if (!Office.context.mailbox.item.getSharedPropertiesAsync) {
                return resolve('');
            }
            try {
                Office.context.mailbox.item.getSharedPropertiesAsync(
                    {asyncContext: {}},
                    function (sharedProperties) {
                        if (sharedProperties.status !== Office.AsyncResultStatus.Succeeded ||
                            (sharedProperties.value.delegatePermissions & Office.MailboxEnums.DelegatePermissions.Write) === 0) {
                            return resolve('');
                        }
                        resolve(sharedProperties.value.targetMailbox);
                    }
                );
            } catch (ex) {
                ex.message = "getSharedUserMail failed: " + ex.message;
                throw ex;
            }
        });
    }

    static isSafari() {
        const ua = navigator.userAgent.toLowerCase();
        return (ua.indexOf('safari') !== -1 && ua.indexOf('chrome') === -1);
    }
}