import * as moment from "moment";
import { appId, addInScope } from "../config"
import { OutlookHelper } from "./OutlookHelper";
import { Ph2ApiHandler } from "./Ph2ApiHandler";
import { createNestablePublicClientApplication } from '@azure/msal-browser'

//This is for authentication code grant flow
export class Authenticator {

    static tokenExchangeFail = 'TokenExchangeFail';
    static refreshTokenLifeTime = 90 // 90 days;

    static async _callGetTokenAndStore(ph2Handler, data, currentDate) {
        if(!(ph2Handler instanceof Ph2ApiHandler) || !ph2Handler){
            throw new Error("ph2Handler must be an object of type Ph2ApiHandler and cannot be null")
        }

        const graphToken = await ph2Handler.getGraphToken(data);
        if (!graphToken || graphToken.error || graphToken.error_description) {
            console.log(graphToken && JSON.stringify(graphToken));
            throw new Error(this.tokenExchangeFail + ' : Error exchange graph token with server, ' + (graphToken && graphToken.error) + ' , ' + (graphToken && graphToken.error_description))
        }

        const expired_at = currentDate.add(graphToken.ext_expires_in - 600, 'second');
        graphToken.expired_at = expired_at.toISOString();
        const settingValue = { raw: graphToken, type: "graph" };
        await OutlookHelper.checkEmailToken(graphToken.id_token);
        await OutlookHelper.setLocalStorage(Ph2ApiHandler.graph, settingValue);

        return settingValue
    }

    static async process(ph2Handler) {
        if (Office.context.platform) {
            // Switch to using @azure/msal-browser instead of @microsoft/office-js-helpers
            // Exclude Outlook desktop and Safari as @azure/msal-browser does not support Internet Explorer
            return await this.authenticate();
        }
        //this need backend to do Authentication code grant flow
        if(!(ph2Handler instanceof Ph2ApiHandler) || !ph2Handler){
            throw new Error("ph2Handler must be an object of type Ph2ApiHandler and cannot be null")
        }
        const currentDate = moment.utc();
        let tokenSetting = ph2Handler.getUserCustomData(Ph2ApiHandler.graph);

        try {
            const expiredDate = tokenSetting && moment.utc(tokenSetting.raw.expired_at);
            // not found token setting || refresh token expired
            if (!tokenSetting || !tokenSetting.raw.refresh_token || moment.duration(currentDate.diff(expiredDate)).asDays() >= this.refreshTokenLifeTime) {
                console.log("Authenticate...");
                //new case, need to promt user to login to get authorization code
                const authorization = await OutlookHelper.promptLogin("code");
                if (!authorization) { throw new Error("User need to do login for permission to use the addin") }

                //exchange authorization code to get graph token
                tokenSetting = await this._callGetTokenAndStore(ph2Handler, {
                    is_auth: 1,
                    token: authorization.raw.code,
                    client_id: appId,
                }, currentDate);
                
                return tokenSetting
            }

            // access token expired
            if (expiredDate <= currentDate) {
                console.log("Graph token is expired, refresh new token");
                tokenSetting = await this._callGetTokenAndStore(ph2Handler, {
                    is_auth: 0,
                    token: tokenSetting.raw.refresh_token,
                    client_id: appId,
                }, currentDate);

                return tokenSetting
            } else {
                // - if token is not expired yet
                console.log('Token is still valid');
                return tokenSetting
            }

        } catch (ex) {
            if(ex && ex.message && ex.message.indexOf(this.tokenExchangeFail) >= 0){
                await OutlookHelper.showError("Please retry login, it seem you have long time no use the addin or there is something wrong.");
                await OutlookHelper.removeLocalStorage(Ph2ApiHandler.graph);
            }
            ex.message = "Authenticate failed: " + ex.message;
            throw ex;
        }    
    }

    static async authenticate() {
        try {
            const msalConfig = {
                auth: {
                    clientId: appId
                }
            };
            const request = {
                scopes: addInScope.split(" "),
                loginHint: Office.context.mailbox.userProfile.emailAddress
            };

            const pca = await createNestablePublicClientApplication(msalConfig);
            const activeAccount = pca.getActiveAccount();

            if (!activeAccount || activeAccount.username != request.loginHint) {
                try {
                    // Attempt SSO silent login using loginHint
                    const ssoResponse = await pca.ssoSilent(request);
                    pca.setActiveAccount(ssoResponse.account);
                    return {
                        raw: {
                            access_token: ssoResponse.accessToken
                        }
                    };
                } catch {
                    const loginResponse = await pca.loginPopup(request);
                    pca.setActiveAccount(loginResponse.account);
                }
            }

            const tokenResponse = await pca.acquireTokenSilent(request);
            
            return {
                raw: {
                    access_token: tokenResponse.accessToken
                }
            };
        } catch (ex) {
            ex.message = "Authenticate failed: " + ex.message;
            throw ex;
        }
    }
}